.flex-container {
  display: flex;
  justify-content: space-between;
}
body {
  background-image: url(data:image/jpeg;base64,);
  background-size: cover;
}
body header {
  height: 60px;
  margin-bottom: 40px;
  padding: 0 20px;
  line-height: 60px;
}
body header .header_headline {
  font-size: 50px;
  margin: 8% 12%;
}
body header .header_headline .red-text {
  color: #6F0000;
}
body .search-form_header {
  margin: 0 12% 2%;
  font-size: 24px;
  text-decoration: underline;
}
body .search-form_create {
  margin-top: 30px;
  display: block;
  height: 38px;
  color: #6F0000;
  border: 1px solid #6F0000;
  background-color: unset;
  border-radius: 5%;
}
body .search-form_create:focus {
  outline: none;
  box-shadow: none;
}
body .search-form_create:hover {
  background-color: #999999;
  border: 0;
  color: white;
  cursor: pointer;
}
body .tasksItemBox {
  display: block;
  float: left;
  width: 200px;
  height: 200px;
  margin: 20px;
  padding: 10px;
  border: 1px #999 solid;
  background-color: #fff;
}
body .tasksItemBox .editable {
  outline: -webkit-focus-ring-color 2px;
}
body .tasksItemBox .options-row {
  margin-top: 55%;
  width: 100%;
}
body .tasksItemBox .options-row .options-active {
  margin-top: -13%;
}
body .tasksItemBox .options-row .badge {
  cursor: pointer;
}
body .tasksItemBox .options-row .display_priority {
  position: static;
  height: fit-content;
}
body .popup {
  position: fixed;
  top: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
body .popup-form {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  width: 400px;
  height: 320px;
  margin: auto;
  padding: 20px;
  box-sizing: initial;
  background-color: #fff;
}
body .popup-form textarea {
  resize: none;
}
